import React, { useEffect, useRef, useState } from "react";
import { Box, Typography, Button, Grid } from "@mui/material";
import PhoneIcon from '@mui/icons-material/Phone';
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import { setPopup } from "../../../store/Slices/popupSlice";
import Images from "src/assets";

const HeroSection = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const recommendations = useAppSelector((state) => state.recommendations);
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.1 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current);
      }
    };
  }, []);

  const featureCards = [
    {
      title: "Self-Apply Path",
      description: "Explore & apply to universities directly",
      icon: "🎓",
      color: "#75ECE5"
    },
    {
      title: "Visa Guidance",
      description: "Step-by-step help with your visa journey",
      icon: "📋",
      color: "#D1D3F9"
    },
    {
      title: "Fast-Track Admission",
      description: "Priority routes to quick enrollment",
      icon: "🏛️",
      color: "#FFA89C"
    },
    {
      title: "Smart Suggestions",
      description: "AI-driven picks for best-fit universities",
      icon: "🧠",
      color: "#75ECE5"
    },
    {
      title: "Anytime Help",
      description: "Expert support whenever you need it",
      icon: "🎧",
      color: "#D1D3F9"
    },
    {
      title: "Scholarship",
      description: "Explore global scholarships and aid options",
      icon: "💰",
      color: "#FFA89C"
    }
  ];

  return (
    <Box
      ref={sectionRef}
      sx={{
        background: "#FAEAD0",
        minHeight: { xs: "auto", lg: "100vh" },
        display: "flex",
        alignItems: "center",
        py: { xs: 4, lg: 8 },
        opacity: isVisible ? 1 : 0,
        transform: isVisible ? "translateY(0)" : "translateY(50px)",
        transition: "all 0.8s ease-out"
      }}
    >
      <Box
        sx={{
          maxWidth: "1440px",
          width: "100%",
          mx: "auto",
          px: { xs: 2, md: 4, lg: 6 }
        }}
      >
        <Grid container spacing={4} alignItems="center">
          {/* Left Section - Text and CTA */}
          <Grid item xs={12} lg={6}>
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                gap: 3,
                opacity: isVisible ? 1 : 0,
                transform: isVisible ? "translateX(0)" : "translateX(-50px)",
                transition: "all 0.8s ease-out 0.2s"
              }}
            >
              {/* Headline */}
              <Typography
                variant="h1"
                sx={{
                  fontSize: { xs: "2.5rem", md: "3.5rem", lg: "4rem" },
                  fontWeight: 700,
                  color: "#2C3E50",
                  lineHeight: 1.2,
                  mb: 2
                }}
              >
                Plan Your Study Abroad Journey —{" "}
                <span style={{ color: "#FF6B35" }}>Your Way</span>
              </Typography>

              {/* Description */}
              <Typography
                variant="body1"
                sx={{
                  fontSize: { xs: "1rem", md: "1.125rem" },
                  color: "#5A6C7D",
                  lineHeight: 1.6,
                  mb: 3
                }}
              >
                Whether you want to explore on your own or get expert help, we've got everything you need — from smart university matches to visa support, all in one place.
              </Typography>

              {/* CTA Buttons */}
              <Box
                sx={{
                  display: "flex",
                  flexDirection: { xs: "column", sm: "row" },
                  gap: 2,
                  mb: 4
                }}
              >
                <Button
                  variant="contained"
                  sx={{
                    background: "#FF6B35",
                    color: "white",
                    borderRadius: "25px",
                    px: 4,
                    py: 1.5,
                    fontSize: "1rem",
                    fontWeight: 600,
                    textTransform: "none",
                    "&:hover": {
                      background: "#E55A2B"
                    }
                  }}
                  onClick={() => {
                    if (recommendations.responseStatus === "recieved" && recommendations.data?.criteria !== undefined) {
                      navigate(`/profile/recommendedCourses/generate`);
                    } else {
                      navigate(`/startpage?`);
                    }
                  }}
                >
                  Do it yourself
                </Button>

                <Button
                  variant="outlined"
                  startIcon={<PhoneIcon />}
                  sx={{
                    border: "2px solid #FF6B35",
                    color: "#2C3E50",
                    borderRadius: "25px",
                    px: 4,
                    py: 1.5,
                    fontSize: "1rem",
                    fontWeight: 600,
                    textTransform: "none",
                    "&:hover": {
                      border: "2px solid #E55A2B",
                      background: "rgba(255, 107, 53, 0.05)"
                    }
                  }}
                  onClick={() => {
                    dispatch(setPopup({
                      show: true,
                      data: {
                        container: { name: "callback", dimensions: { width: "500px" } },
                        type: "custom",
                      },
                    }));
                  }}
                >
                  Talk to expert
                </Button>
              </Box>

              {/* App Download */}
              <Box>
                <Typography
                  variant="body2"
                  sx={{
                    color: "#7F8C8D",
                    fontSize: "0.875rem",
                    mb: 2
                  }}
                >
                  Download our App for Easy Management
                </Typography>
                <Box
                  sx={{
                    display: "flex",
                    gap: 2,
                    flexDirection: { xs: "column", sm: "row" }
                  }}
                >
                  <img 
                    src={Images.googlePlay} 
                    alt="Google Play" 
                    style={{ 
                      height: "40px", 
                      cursor: "pointer" 
                    }}
                    onClick={() => window.open("https://play.google.com/store/apps/details?id=com.onewindow.app&pcampaignid=web_share", "_blank")}
                  />
                  <img 
                    src={Images.appStore} 
                    alt="App Store" 
                    style={{ 
                      height: "40px", 
                      cursor: "pointer" 
                    }}
                    onClick={() => window.open("https://apps.apple.com/in/app/one-window/id6670320345", "_blank")}
                  />
                </Box>
              </Box>
            </Box>
          </Grid>

          {/* Right Section - Feature Cards Grid */}
          <Grid item xs={12} lg={6}>
            <Grid container spacing={2}>
              {featureCards.map((card, index) => (
                <Grid item xs={12} sm={6} key={index}>
                  <Box
                    sx={{
                      background: `${card.color}`,
                      borderRadius: "16px",
                      p: 3,
                      height: "80px",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "space-between",
                      boxShadow: "0 4px 20px rgba(0,0,0,0.08)",
                      border: `2px solid ${card.color}20`,
                      transition: "all 0.3s ease",
                      opacity: isVisible ? 1 : 0,
                      transform: isVisible ? "translateY(0)" : "translateY(30px)",
                      animation: isVisible ? `fadeInUp 0.6s ease-out ${0.4 + index * 0.1}s both` : "none",
                      "&:hover": {
                        transform: "translateY(-4px)",
                        boxShadow: "0 8px 30px rgba(0,0,0,0.12)",
                        border: `2px solid ${card.color}40`
                      },
                      "@keyframes fadeInUp": {
                        "0%": {
                          opacity: 0,
                          transform: "translateY(30px)"
                        },
                        "100%": {
                          opacity: 1,
                          transform: "translateY(0)"
                        }
                      }
                    }}
                  >
                    <Box>
                      <Typography
                        variant="h6"
                        sx={{
                          fontSize: "1.125rem",
                          fontWeight: 600,
                          color: "#2C3E50",
                          mb: 1
                        }}
                      >
                        {card.title}
                      </Typography>
                      <Typography
                        variant="body2"
                        sx={{
                          color: "#7F8C8D",
                          fontSize: "0.875rem",
                          lineHeight: 1.4
                        }}
                      >
                        {card.description}
                      </Typography>
                    </Box>
                    <Typography
                      sx={{
                        fontSize: "2rem",
                        alignSelf: "flex-end"
                      }}
                    >
                      {card.icon}
                    </Typography>
                  </Box>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default HeroSection; 